import { useLocation, useNavigate } from 'react-router-dom';

import { ForgotPassArrowIcon } from '../../assets';
import { AuthLayout, ResetPasswordForm, SendEmailForm, StatusBlock } from '../../components';
import { ROUTE } from '../../routes/routes';
import { fetchResetPassword, getForgotPasswordInfo, useAppDispatch, useAppSelector } from '../../store';

import { BackToAuth } from './styles';

export const ForgotPasswordPage = () => {
  const { isSendEmail, isResetPassword, errorResetPasswordMessage, dataRequestReset } =
    useAppSelector(getForgotPasswordInfo);
  const { search } = useLocation();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  return isSendEmail || isResetPassword || errorResetPasswordMessage ? (
    isSendEmail ? (
      <StatusBlock
        noButton={true}
        title=' '
        message='   ,      '
      />
    ) : isResetPassword ? (
      <StatusBlock
        title='  '
        message='   ,      '
        buttonText=''
        onClick={() => {
          navigate(ROUTE.AUTH);
        }}
      />
    ) : (
      <StatusBlock
        gapSm={125}
        gapLg={159}
        title='  '
        message='-   .   '
        buttonText=''
        onClick={() => {
          dispatch(fetchResetPassword(dataRequestReset));
        }}
      />
    )
  ) : search ? (
    <AuthLayout title=' '>
      <ResetPasswordForm />
    </AuthLayout>
  ) : (
    <AuthLayout title=' ' paddingTop={96}>
      <BackToAuth to={ROUTE.AUTH}>
        <ForgotPassArrowIcon />    
      </BackToAuth>
      <SendEmailForm />
    </AuthLayout>
  );
};
