import { useNavigate } from 'react-router-dom';

import { AuthLayout, RegistrationForm, StatusBlock } from '../../components';
import { ROUTE } from '../../routes/routes';
import {
  clearRegistration,
  fetchRegistrationUser,
  getRegistrationUserInfo,
  useAppDispatch,
  useAppSelector,
} from '../../store';

export const RegistrationPage = () => {
  const dispatch = useAppDispatch();
  const { user, isRegistration, errorRegistrationMessage, errorRegistrationStatus } =
    useAppSelector(getRegistrationUserInfo);
  const navigate = useNavigate();

  return isRegistration ? (
    <StatusBlock
      title=' '
      message='  .    ,     '
      buttonText=''
      onClick={() => {
        navigate(ROUTE.AUTH);
      }}
    />
  ) : errorRegistrationMessage ? (
    errorRegistrationStatus && errorRegistrationStatus === 400 ? (
      <StatusBlock
        gapLg={135}
        title='  '
        message='   e-mail    .       e-mail'
        buttonText='  '
        onClick={() => {
          dispatch(clearRegistration());
        }}
      />
    ) : (
      <StatusBlock
        gapSm={115}
        title='  '
        message='-        .   '
        buttonText=''
        onClick={() => {
          dispatch(fetchRegistrationUser(user));
        }}
      />
    )
  ) : (
    <AuthLayout title=''>
      <RegistrationForm />
    </AuthLayout>
  );
};
